.stc {
    &__img-container {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 50px;
    }

    &__img {
        width: 33%;
        overflow: hidden;
        img {
            border-radius: 8px;
        }
    }
}

.s2c-media {
    margin-bottom: 50px;

    &__link {
        display: flex;
        // gap: 20px;
        background-color: $white;
        border: 1px solid #eee;
        border-radius: 8px;
        overflow: hidden;
        margin-bottom: 10px;
        &-img {
            height: 120px;

            img {
                height: 100%;
            }
        }

        &-title {
            font-size: 1.2rem;
            font-weight: bold;
        }
        &-text {
        }
    }

    &__video-slider {
        width: 100%;
        margin-bottom: 30px;

        iframe {
            max-width: 100%;
            width: 100%;
        }

        .splide__track,
        .splide__slide {
            padding: 0;
        }
    }
    &__inner {
        justify-content: space-between;
        align-items: start;

        @media screen and (min-width: 992px) {
            display: flex;
            width: 100%;
            max-width: 100%;
            gap: 20px;
        }
    }
    &__slide {
        text-align: center;
    }
}
