.section {
    // height: 100vh;
    // display: flex;
    align-items: center;
    padding: 100px 30px;

    @media screen and (min-width: 900px) {
        padding: 100px 30px;
    }
    &__title {
        font-size: 2rem;
        // border-bottom: 2px solid;
        margin-bottom: 30px;
        font-family: $sectionTitleFont;
        // font-family: "Oswald", sans-serif;
        // text-transform: uppercase;
        font-weight: 900;
        color: $primeOrange;
        line-height: 32px;
        // text-align: center;
        &--blue {
            color: #3e8999;
        }
        // &--orange {
        //     color: $primeOrange;
        // }
        // &--purple {
        //     color: $primePurple;
        // }
        // &--green {
        //     color: $primeGreen;
        // }

        @media screen and (min-width: 900px) {
            font-size: 2.1rem;
            text-align: left;
        }

        &--center {
            text-align: center;
        }

        &--small {
            font-size: 1.5rem;
        }
    }

    &__subtitle {
        &--blue {
            color: #3e8999;
        }
    }

    &--interior {
        padding-top: 50px;

        @media screen and (min-width: 900px) {
            // padding-top: 50px;
        }
    }

    &--contact {
        @media screen and (min-width: 900px) {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            align-items: flex-start;
            &__col1 {
                width: 60%;
                padding-right: 50px;
            }
            &__col2 {
                width: 40%;
            }
        }
    }

    p {
        font-size: 1.2rem;
        line-height: 30px;
        margin-bottom: 20px;
        color: #1c1c1c;
    }

    &__link {
        margin-bottom: 20px;
        font-size: 1.2rem;
        line-height: 30px;
        display: inline-block;
    }
}
