.hero {
    // border-radius: 20px;
    // background-color: $primeOrange;
    // box-shadow: 0px 0px 62px rgba(0,0,0,0.1);
    // background: url('/dist/img/Caden.jpg') no-repeat ;
    background-size: cover;
    // margin-top: 20px;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: auto;
    // display: flex;
    align-items: center;
    // padding-top: 150px;
    padding: 0 0 100px 0;

    @media screen and (min-width: 900px) {
        height: 700px;
        min-height: 700px;
        padding: 0;
    }

    &--interior {
        padding: 0;
        @media screen and (min-width: 900px) {
            height: 35vh;
            min-height: unset;
            padding: 0;
        }
    }

    @media screen and (max-height: 750px) {
        // height: 60vh;
        // min-height: 900px;
    }

    &__inner {
        height: 100%;
        align-items: center;
        justify-content: left !important;
        position: relative;
    }

    &__overlay {
        background: #37190e;
        // background: linear-gradient(153deg, rgba(231,97,0,0.6082808123249299) 0%, rgba(182,65,29,1) 100%);
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 2;
        opacity: 0.5;
        // display: none;
    }

    &--bsi {
        .hero__overlay {
            background-color: #3e8999 !important;
            opacity: 0.9;
        }
    }

    // &--green {
    //     .hero__overlay {
    //         background: #366b4b;
    //         opacity: 0.9;
    //     }

    //     .hero__opener {
    //         animation-duration: 2.5s;
    //         animation-delay: 0.3s;
    //     }
    // }

    &__opener {
        width: 100%;
        max-width: 100%;
        text-align: left;
        position: relative;
        z-index: 10;
        padding: 0 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        opacity: 0;
        animation: 1s ease 0s normal forwards 1 fadein;
        margin: 120px 0 20px 0;

        @media screen and (min-width: 900px) {
            margin: 50px 0;
            transform: translateY(50px);
        }

        @keyframes fadein {
            0% {
                opacity: 0;
                // transform: translateY(150px);
            }

            16% {
                opacity: 0;

                // transform: translateY(150px);
            }

            100% {
                opacity: 1;
                // transform: translateY(50px);
            }
        }

        @media screen and (min-width: 900px) {
            justify-content: end;
            // padding-right: 50px;
            // text-align: right;
            height: auto;
            display: block;
        }
    }
    &__title {
        color: #fbf3eb;
        // text-transform: uppercase;
        // font-weight: 700;
        font-family: $sectionTitleFont;
        font-size: 2.5rem;
        line-height: 42px;
        // text-stroke: 1px $primeOrange;
        // -webkit-text-stroke: 1px $primeOrange;
        margin-bottom: 20px;
        // text-shadow: 2px 2px 7px rgba(0, 0, 0, 0.5);
        transition-duration: 0.3s;

        span {
            text-transform: none;
            font-weight: 100;
            font-family: $bodyFont;
            font-size: 2rem;
            margin-top: 10px;
            display: block;
            // letter-spacing: -1px;
            transition-duration: 0.3s;
            line-height: 35px;
            // -webkit-text-stroke-width: 1px;
            // -webkit-text-stroke-color: transparent;
            // color: transparent;
        }

        @media screen and (min-width: 1200px) {
            // font-size: 3.2rem;

            span {
                font-size: 1.8rem;
                line-height: 38px;
            }
        }

        // @media screen and (max-height: 750px) {
        //     font-size: 3rem;

        //     span {
        //         font-size: 1.8rem;
        //     }
        // }
    }

    &__subtitle {
        color: #eee;
        font-size: 2rem;
    }

    &__byline {
        color: #eee;
        margin-bottom: 20px;
        font-size: 1.2rem;
    }

    &__cta {
        font-size: 2rem;
        text-decoration: none;
        background-color: $primeOrange;
        color: #fff;
        padding: 15px 20px;
        border-radius: 100px;
        display: inline-block;
        transition: 0.3s;
        margin-top: 20px;
        text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);

        &-icon {
            width: 30px;
        }

        i {
            font-size: 1.5rem;
            opacity: 0;
            width: 0;
            transition-duration: 0.3s;
            position: relative;
        }

        &:hover {
            // background-color: darken($primeOrange, 10%);
            color: $white;
            padding-right: 50px;
            i {
                opacity: 1;
                transform: translateX(5px);
            }
        }

        @media screen and (min-width: 1200px) {
            font-size: 1.5rem;
            i {
                font-size: 1.5rem;
            }
        }
    }

    &__footer {
        position: relative;
        bottom: 0;
        left: 0;
        width: 100%;
        z-index: 1999;
        width: 100%;
        text-align: center;
        animation: 1s ease 0s normal forwards 1 fadein;
        padding: 0 30px;
        margin-top: 100px;
        @media screen and (min-width: 900px) {
            position: absolute;
            margin-top: unset;
        }

        &-title {
            color: #fff;
            font-size: 1.2rem;
            padding: 10px;
            text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
            text-transform: uppercase;
            width: 300px;
            display: block;
            margin: 0 auto;
            // background: #d57418;
            border-top-right-radius: 8px;
            border-top-left-radius: 8px;
            // display: none;
        }
        &-cards {
            justify-content: center;
            padding: 0px;
            // background: #9f6a37;
            // background: $primeOrange;
            // mask-image: linear-gradient(to right, transparent, black 20%, black 80%, transparent);
            // -webkit-mask-image: linear-gradient(
            //     to right,
            //     transparent,
            //     black 20%,
            //     black 80%,
            //     transparent
            // ); /* For WebKit browsers */
            gap: 10px;
            align-items: end;
            @media screen and (min-width: 900px) {
                display: flex;
            }
        }
        &-card {
            color: #fff;
            font-size: 1.3rem;
            padding: 10px;
            text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
            background: #b76544;

            // margin: 0 1px;
            position: relative;
            transition-duration: 0.3s;
            // &:hover {
            //     padding-bottom: 15px;
            // }

            &:nth-child(1) {
                background: #549982;
                border-top-left-radius: 8px;
                border-top-right-radius: 8px;
            }

            &:nth-child(3) {
                background: #b74454;
            }

            &:nth-child(4) {
                background: #c39f49;
            }

            &:nth-child(5) {
                background: #4461b7;
                border-bottom-left-radius: 8px;
                border-bottom-right-radius: 8px;
            }

            @media screen and (min-width: 900px) {
                font-size: 1rem;
                border-top-left-radius: 8px;
                border-top-right-radius: 8px;
                &:nth-child(5) {
                    border-bottom-left-radius: 0px;
                    border-bottom-right-radius: 0px;
                }
            }

            @media screen and (min-width: 1200px) {
                font-size: 1.2rem;
            }

            // &::after {
            //     content: "";
            //     right: -4px;
            //     position: absolute;
            //     display: none;
            // }

            @media screen and (min-width: 900px) {
                padding: 10px 20px;

                // &::after {
                //     content: "";
                //     right: -4px;
                //     position: absolute;
                //     display: inline-block;
                // }
            }

            &:last-child {
                &::after {
                    display: none;
                }
            }
        }
    }
}

#hero-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; /*  Ensure the video covers the entire area */
    z-index: 1; /*  Place the video behind the content */
}

.hero-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    text-align: center;
    z-index: 1; /*  Ensure content is above the video */
    /* Add styling for your content here */
}
