.dark {
    background-color: $bodydark;
    // $primeOrange: #584da1;
    .header {
        @media screen and (max-width: 900px) {
            // background-color: darken($bodydark, 5%);
        }
        background-color: lighten($bodydark, 5%);

        border: solid 1px #1f1a3c;
    }

    .header__logo-img {
        filter: invert(1) hue-rotate(160deg) saturate(0.8);
    }

    .main-nav__link {
        color: #eee;
        &--active {
            color: $primeOrange;
        }
    }

    .hero {
        &__overlay {
            background: darken($bodydark, 2%);
        }
    }

    .hero__cta {
        color: $white;
        &:hover {
            color: $white;
        }
    }

    p {
        color: #eee;
    }

    .team__modal-close-icon {
        color: lighten($bodydark, 15%);

        outline: $bodydark 1px;
    }

    .team__readmore {
        color: $primeOrange;
    }

    .aoe__also,
    .aoe__title,
    .pricing__ul li,
    .team__title,
    .form__req-instruction,
    .form__field,
    .form__non-text-label,
    .form__radio-label,
    .wmud__li,
    .faq__ul li {
        color: #eee;
    }

    .aoe__card,
    .testimonial__card,
    .testimonial__author,
    .team__card,
    .accordion__q,
    .form__input,
    .btt,
    .team__modal-card,
    #chart-wrap .legend .legend-group,
    .s2c-media__link {
        background-color: lighten($bodydark, 5%);
        border-color: $bodydark;
        color: #eee;
    }

    .testimonial__readMoreLink {
        color: $primeOrange;
        &::after {
            display: none;
        }
        &:hover {
            color: darken($primeOrange, 20%);
        }
    }

    .form__input--submit {
        background-color: darken($primeGreen, 10%);
        &:hover {
            background-color: darken($primeGreen, 20%);
        }
    }

    .form__label {
        background-color: lighten($bodydark, 5%);

        color: #eee;
        // box-shadow: 0 0 2px 4px #302a55;

        &--focus {
            border: 1px solid #1f1a3c;
        }
    }

    .main-nav__hamburger i {
        color: #eee;
    }

    .footer-nav__link {
        color: $white;

        &:hover {
            color: $white;
        }
    }

    a {
        color: lighten($primeBlue, 20%);

        &:hover {
            color: $primeOrange;
        }
    }

    .footer__link {
        color: #eee;
        &:hover {
            color: #eee;
        }
    }

    .bsi__ul li {
        color: #eee;
    }
}
.main-body.dark::before {
    content: "";
    position: absolute; /* Positions the pseudo-element relative to the container */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("../../dist/img/Beyond-speech-icon-darker.png");
    background-repeat: repeat;
    opacity: 0.01; /* **This sets the opacity of only the background image layer** */
    z-index: -1; /* Pushes the pseudo-element behind the main content */
}

body.darkMode {
    &::-webkit-scrollbar {
        width: 8px;
        background: $bodydark;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $primePurple;
        border-radius: 20px;
    }

    &::-moz-scrollbar {
        width: 8px;
        background: $bodydark;
    }

    &::-moz-scrollbar-thumb {
        background-color: $primePurple;
        border-radius: 20px;
    }
}
