.team {
    &__modal-wrapper {
        justify-content: center;
        display: none;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        transition: 0.3s ease;

        &--active {
            display: flex !important;
            top: 0;
            left: 0;
        }
    }
    &__modal-overlay {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.75);
        z-index: 1001;
        animation: fadein 0.5s ease;
    }

    @keyframes fadein {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }

    &__modal-inner {
        position: fixed;
        top: 50%;
        transform: translateY(-50%);
        margin: 0 auto;
        right: auto;
        z-index: 1010;
        padding: 20px;
        width: auto;
        height: 100%;
        max-width: 100%;
        width: 1200px;
        animation: fadein 0.5s ease;
        overflow: scroll;

        @media screen and (min-width: 900px) {
            height: auto;
            overflow: visible;
        }
    }

    &__modal-close-icon {
        position: absolute;
        top: 5px;
        right: 5px;
        cursor: pointer;
        color: #d4d4d4;
        font-size: 2rem;
        z-index: 100;
        transition: 0.3s ease;

        &:hover {
            transform: scale(1.2);
        }
    }

    &__modal-card {
        // text-align: center;
        // height: 100%;
        max-width: 100%;
        padding: 30px;
        border-radius: 20px;
        margin-bottom: 20px;
        border: solid 1px #e2e2e2;
        background-color: #fff;

        @media screen and (min-width: 900px) {
            padding: 50px;
            align-items: center;
            text-align: left;
            display: flex;
            justify-content: space-between;
        }
        p {
            line-height: 28px;
            font-size: 1.2rem;
        }
        .team__img {
            border-radius: 20px;
        }

        &--vertical {
            display: flex;
            flex-direction: column;

            .team__content {
                width: 100%;
            }
        }
    }

    &__container {
        // border: solid 1px $primeBlue;
        // padding: 20px 0;
        border-radius: 20px;
        gap: 20px;
        margin-bottom: 20px;
        // background-color: lighten($primeBlue, 46%);

        @media screen and (min-width: 900px) {
            // padding: 20px 0;
        }
    }

    &__row {
        @media screen and (min-width: 900px) {
            display: flex;
            gap: 20px;
            // align-items: flex-start;
        }

        .team__card {
            display: block;
            padding: 20px;
            // box-shadow: 2px 2px 22px rgba(159, 78, 45, 0.07);

            p {
                font-size: 1.1rem;
                line-height: 24px;
            }
        }

        .team__content {
            width: 100%;
        }

        // .team__title {
        //     font-size: 1.5rem;
        // }

        .team__roles {
            font-size: 1rem;
        }

        .team__img {
            height: auto;
            width: 100%;
            background: none;
        }
    }

    &__readmore {
        color: $primeOrange;
        cursor: pointer;
        transition: 0.3s ease;

        &:hover {
            color: darken($primeOrange, 20%);
        }
    }

    &__card {
        // text-align: center;
        // height: 100%;
        max-width: 100%;
        padding: 30px;
        border-radius: 8px;
        margin-bottom: 20px;
        border: solid 1px #e2e2e2;
        background-color: #fff;

        @media screen and (min-width: 900px) {
            padding: 50px;
            width: 100%;
            text-align: left;
            display: flex;
            justify-content: space-between;
        }
        p {
            line-height: 28px;
            font-size: 1.2rem;
        }

        &--vertical {
            display: flex;
            flex-direction: column;

            .team__content {
                width: 100%;
            }

            .team__img {
                width: 60%;
                margin: 0 auto 50px;
            }
        }

        &--placeholder {
            opacity: 0;
        }
    }

    &__img {
        margin: 0 auto 50px;
        overflow: hidden;
        height: 100%;
        width: 60%;

        img {
            object-fit: cover;
            height: 100%;
            width: 100%;
            max-width: 100%;
            border-radius: 8px;
        }

        @media screen and (min-width: 900px) {
            margin: 0 50px 20px auto;
            width: 30%;
        }
    }

    &__content {
        width: 100%;
        margin: 0 auto;

        @media screen and (min-width: 900px) {
            width: 70%;
        }
    }

    &__title {
        font-size: 2rem;
        color: #222222;
        margin-bottom: 10px;
        font-family: $titleFont;
        @media screen and (min-width: 900px) {
            font-size: 1.8rem;
        }
    }

    &__roles {
        display: block;
        margin-bottom: 20px;
        color: $primeOrange;
        font-size: 1.2rem;
        line-height: 23px;
    }

    &__also {
        color: #222222;
        line-height: 25px;
        width: 800px;
        max-width: 100%;
        margin: 0 auto;
        font-size: 1rem;

        @media screen and (min-width: 900px) {
            font-size: 1.1rem;
        }
    }
}
