.bsi {
    &__opener {
        padding-bottom: 50px;
        @media screen and (min-width: 992px) {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-bottom: 0;
            div {
                width: 50%;
            }
        }
    }
    &__collage {
        img {
            object-fit: cover;
            max-height: 500px;
            width: 100%;
        }
    }

    &__media {
        justify-content: space-between;
        gap: 50px;
        margin-bottom: 50px;
        display: flex;
        flex-direction: column-reverse;
        @media screen and (min-width: 992px) {
            flex-direction: row;
        }
        &-col {
            @media screen and (min-width: 992px) {
                width: 50%;
            }
        }
    }

    &__donate-btn {
        font-size: 1.5rem;
        padding: 10px 20px;
        border: 1px solid #3e8999;
        border-radius: 8px;
        color: #3e8999 !important;
        display: inline-block;
        transition: 0.3s ease;
        width: 100%;
        text-align: center;
        @media screen and (min-width: 992px) {
            width: auto;
        }
        // margin-top: 20px;
        i {
            font-size: 1.5rem;
            opacity: 0;
            width: 0;
            transition-duration: 0.3s;
            position: relative;
        }
        &:hover {
            color: #3e8999;
            padding-right: 50px;

            i {
                opacity: 1;
                transform: translateX(5px);
            }
        }
    }

    &__ul {
        @media screen and (min-width: 992px) {
            padding-left: 30px;
        }
        li {
            font-size: 1.2rem;
            margin-bottom: 20px;
            line-height: 25px;
            color: #222222;
            list-style: none;

            &::before {
                content: "\2022"; /* Add content: \2022 is the CSS Code/unicode for a bullet */
                color: #3e8999; /* Change the color */
                font-weight: bold; /* If you want it to be bold */
                display: inline-block; /* Needed to add space between the bullet and the text */
                width: 1.2rem;
                margin-left: 0em;
                font-size: 2rem;

                @media screen and (min-width: 900px) {
                    width: 2rem;
                    margin-left: -1em; /* Also needed for space (tweak if needed) */
                }
            }
        }
    }
}
