#chart-wrap {
    display: flex;
    flex-direction: column;
    height: auto;
    justify-content: center;
    margin: auto;
    position: relative;
    width: 100%;

    .chart-tooltip {
        margin-left: 15px;
        position: absolute;
        z-index: 10;

        .chart-tooltip-wrap {
            background-color: #181818;
            border-radius: 10px;
            box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
            display: block;
            padding: 0.875rem;

            p {
                color: #fff;
                font-size: 0.875rem;
                line-height: 1.75;
                margin: 0;
            }
        }
    }

    svg {
        margin: auto;

        .text {
            fill: #fff;
            font-size: 1.3rem;
            text-anchor: middle;
            font-weight: bold;
            text-shadow: 1px 1px 0px #000;
        }
    }

    .legend {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 10px;
        justify-content: flex-start;
        margin: 2rem auto;
        width: 100%;

        .legend-group {
            align-items: center;
            display: flex;
            flex-direction: row;
            gap: 8px;
            justify-content: flex-start;
            background-color: #fff;
            border: 1px solid #eee;
            padding: 10px;
            width: 100%;
            border-radius: 8px;

            &:last-child {
                display: none;
            }

            .legend-box {
                height: 20px;
                margin: 0;
                width: 20px;
            }

            .legend-label {
                margin: 0;
            }
        }
    }

    .slices {
        &:hover {
            cursor: pointer;
        }
    }
}
