.form {
    &__req-instruction {
        position: relative;
        display: block;
        display: flex;
        align-items: center;
        font-size: 1.2rem;
    }
    &__req-instruction-icon {
        width: 5px;
        height: 5px;
        background: #e53232;
        border-radius: 100%;
        margin-left: 10px;
    }
    &__form {
        margin-top: 50px;
    }
    &__field {
        width: 100%;
        position: relative;
        margin-bottom: 30px;
        border-radius: 4px;
    }

    &__field-row {
        justify-content: space-between;
        gap: 20px;
        align-items: end;
        @media screen and (min-width: 900px) {
            display: flex;
        }
    }

    &__label {
        display: block;
        margin-bottom: 5px;
        position: absolute;
        top: 10px;
        left: 10px;
        background-color: #fff;
        padding: 4px 4px 5px 4px;
        // box-shadow: 0 0 2px 4px #fff;
        transition:
            transform 0.3s ease,
            padding 0.3s ease,
            left 0.3s ease;
        color: #555;
        pointer-events: none;
        z-index: 10;
        font-size: 1.2rem;
        &--focus {
            padding: 3px 10px 5px;
            border: 1px solid #c0c0c0;
            border-radius: 8px;
            box-shadow: none;
            transform: translateY(-32px);
            color: #000;
            left: -10px;
        }
    }

    &__non-text-label {
        display: block;
        font-size: 1.2rem;
        line-height: 30px;
        margin-bottom: 20px;
        color: #1c1c1c;
        position: relative;
    }

    &__radio-label {
        margin-bottom: 10px;
        font-size: 1.1rem;
        color: #1c1c1c;
    }

    &__input {
        width: 100%;
        outline: none !important;
        border-radius: 4px;
        background: #fff;
        border: none;
        padding: 15px 20px;
        border: 1px solid #c0c0c0;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        border-bottom-color: rgb(192, 192, 192);
        border-bottom: 1px solid #c0c0c0;
        font-size: 1rem;
        &--submit {
            background-color: darken($primeGreen, 10%);
            cursor: pointer;
            transition: 0.3s ease;
            color: #fff;
            text-transform: uppercase;
            display: inline-block;
            width: auto;
            padding: 10px 20px;
            border: none;
            width: 200px;
            font-size: 1.2rem;
            border-radius: 100px;
            // border: 2px solid lighten($primeGreen, 20%);
            // box-shadow: 0px 0px 5px lighten($primeGreen, 10%);
            &:hover {
                background-color: darken($primeGreen, 30%);
            }
        }
    }

    &__border {
        position: absolute;
        bottom: 0;
        left: 0;
        height: 0.15rem;
        background: $primeGreen;
        width: 0%;
        transition-duration: 0.4s;
    }

    &__req {
        position: absolute;
        top: 8px;
        right: 8px;
        width: 5px;
        height: 5px;
        background: #e53232;
        border-radius: 100%;

        &--inline {
            position: relative;
            top: -10px;
            right: unset;
            display: inline-block;
        }
    }

    &__captcha-notice {
        font-size: 1rem !important;
        margin-top: 20px;
        text-align: left;
        text-wrap: balance;
    }
}

.form__field input:focus,
.form__field select:focus,
.form__field textarea:focus {
    box-shadow: 0 0 0 1px #000;
    transition: box-shadow 0.3s ease;
    outline: none;
}

.form__label--focus,
.form__label--multi-focus {
}

.form__field--valid .form__border {
    width: 100%;
}
.colors {
    display: flex;
    flex-direction: column;
}
input[type="radio"] {
    margin-bottom: 10px;
}
